@import 'vars';
@import 'fonts';
@import 'reset';
@import 'libs';
@import 'global';

/* font-family: 'Montserrat',sans-serif;
 font-family: 'Open Sans',sans-serif;
 */
*,
*::after,
*::before {
  box-sizing: border-box;
}

.header {
  background-image: url(../img/header__bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  max-height: 100%;

  &__title {
    max-width: 710px;
    width: 100%;
    font-size: 160px;
    font-weight: 400;
    line-height: 150px;
    color: #FFEA00;
    margin: 211px auto 214px 0;
  }
}

.nav__active {
  display: none;
}

.nav {
  display: flex;
  align-items: center;
  margin-top: 40px;
  width: 100%;

  &__list {
    max-width: 536px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-right: 321px;
    padding-bottom: 31px;
    border-bottom: 1px solid #D9D9D9;
  }

  &__item {}

  &__link {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    letter-spacing: 0.03em;
    text-align: left;
    color: #fff;
  }

  &__contact {
    color: #FFEA00;
    width: 312px;
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__contact-phone {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    text-align: center;
    color: inherit;
    margin-right: 55px;
    /*  margin-bottom: 60px; */
  }

  &__language {}

  &__language-link {
    font-family: Montserrat;
    font-size: 22px;
    font-weight: 300;
    line-height: 27px;
    letter-spacing: 0.03em;
    text-align: left;
    color: inherit;
  }

  &__language-link--active {
    font-weight: 600;
  }
}

.logo {
  width: 50px;
  height: 50px;
}

.rightside-menu {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 284px;
  width: 100%;
  background-color: #383838;
  padding-top: 40px;
  box-shadow: -2px 3px 17px 0px #000;
  transition: all .3s linear;
  z-index: 2;
  overflow: hidden;

  &.rightside-menu--close {
    transform: translateX(500%);
  }

  &__open {
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  &__close {
    background-color: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
    margin-left: 220px;
    margin-bottom: 30px;
  }



}

.footer {
  background-color: #383838;
  border-top: 3px solid rgba(96, 96, 96, 1);

  &__inner {
    display: flex;

  }

  &__nav {
    width: 34.375%;
  }

  &__nav-list {
    height: 90px;
    width: 268px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 5% 47% 10% 0;
  }

  &__nav-item {}

  &__nav-link {
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: #fff;
  }

  &__item {
    width: 31.25%;
    border-right: 3px solid rgba(96, 96, 96, 1);
    border-left: 3px solid rgba(96, 96, 96, 1);
    margin: 0 auto;
  }

  &__contact {
    border-bottom: 3px solid rgba(96, 96, 96, 1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 43px 0 23px;
  }

  &__contact-link {
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: rgba(255, 234, 0, 1);
    text-align: center;
  }

  &__contact-link:first-child {
    margin-bottom: 15px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    padding: 8px 0 24px;
  }

  &__info-img {
    width: 70px;
    height: 70px;

  }

  &__info-text {
    font-size: 20px;
    font-weight: 300;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.1);
    width: 230px;
  }

  &__social {
    width: 34.375%;
  }

  &__social-list {
    height: 80px;
    width: 300px;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    column-gap: 30px;
    margin: 5% 0 10% auto;
  }

  &__social-item {}

  &__social-link {

    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: #fff;
  }
}



.info {
  margin: 70px auto 70px 0;
  color: #3B3B3B;

  &__title {
    font-size: 48px;
    font-weight: 400;
    line-height: 52px;
    max-width: 597px;
    width: 100%;
    margin-bottom: 70px;
  }

  &__text {
    width: 860px;
    font-size: 24px;
    font-weight: 300;
    line-height: 29px;

  }
}


.gallery {
  margin-bottom: 80px;

  &__inner {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 25px;
    height: 2360px;
  }


  &__item:nth-child(1) {
    background-image: url(../img/gallery/gal-1.jpg);

  }

  &__item:nth-child(2) {
    background-image: url(../img/gallery/gal-2.png);

  }

  &__item:nth-child(3) {
    background-image: url(../img/gallery/gal-3.jpg);

    grid-row: span 2;
  }

  &__item:nth-child(4) {
    background-image: url(../img/gallery/gal-4.jpg);

    grid-column: span 2;
  }

  &__item:nth-child(5) {
    background-image: url(../img/gallery/gal-5.jpg);
  }

  &__item:nth-child(6) {
    background-image: url(../img/gallery/gal-6.jpg);
  }

  &__item:nth-child(7) {
    background-image: url(../img/gallery/gal-7.jpg);
  }

  &__item:nth-child(8) {
    background-image: url(../img/gallery/gal-8.jpg);
  }

  &__item:nth-child(9) {
    background-image: url(../img/gallery/gal-9.jpg);
    grid-column: span 2;
  }

  &__item {
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 45px;
    padding-top: 48px;
    position: relative;
    color: #fff;
  }

  &__item-number {

    font-size: 24px;
    font-weight: 300;
    line-height: 29px;

  }

  &__item-title {
    position: absolute;
    bottom: 94px;
    left: 45px;
    font-size: 48px;
    font-weight: 400;
    line-height: 48px;
    text-transform: uppercase;

  }

  &__item-link {
    position: absolute;
    bottom: 47px;
    left: 45px;
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
    color: inherit;
  }

  &__item-img {}

}

.container-gluid {}


.record {
  margin-bottom: 80px;

  &__inner {
    background-image: url(../img/form__bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 914px;
    display: flex;
  }

  &__title-inner {
    width: 44%;
  }

  &__title {
    font-size: 96px;
    font-weight: 400;
    line-height: 96px;
    margin: 30% auto 0 auto;
    width: 593px;
    color: #FFEA00;

  }

  &__content {
    background-color: #4C4A4B;
    width: 56%;
    display: flex;
    flex-direction: column;
    align-items: left;
    padding-left: 5%;
  }

  &__content-title {
    font-size: 48px;
    font-weight: 400;
    line-height: 48px;
    width: 584px;
    margin-bottom: 30px;
    color: #ffea00;
    margin-top: 195px;
  }

  &__content-text {
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    max-width: 400px;
    width: 100%;
    color: #fff;
    margin-bottom: 35px;
  }

  &__content-form {
    background-color: transparent;
    border: 1px solid rgba(95, 94, 92, 1);
    width: 66%;

  }

  &__content-input--name {
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid rgba(95, 94, 92, 1);
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: #fff;
    box-shadow: none;
    padding: 20px 17px 20px 20px;

    &::placeholder {
      font-family: 'Montserrat', sans-serif;
      font-size: 16px;
      font-weight: 300;
      line-height: 20px;
      color: #fff;

    }
  }

  &__content-input--number {
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid rgba(95, 94, 92, 1);
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: #fff;
    box-shadow: none;
    padding: 20px 17px 20px 20px;

    &::placeholder {
      font-family: 'Montserrat', sans-serif;
      font-size: 16px;
      font-weight: 300;
      line-height: 20px;
      color: #fff;

    }
  }

  &__content-select {
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid rgba(95, 94, 92, 1);
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    color: #fff;
    box-shadow: none;
    padding: 20px 17px 20px 20px;
  }

  &__content-button {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;
    background-color: #5F5E5C;
    padding: 20px 57px;
    border: none;
    color: #fff;
  }
}

.reviews {
  background-color: #383838;
  text-align: center;
  padding-bottom: 70px;

  &__title {
    font-size: 64px;
    font-weight: 400;
    line-height: 64px;
    color: #FFEA00;
    padding-top: 122px;
    margin: 0 auto 110px;
    width: 700px;

  }

  &__slider {}

  &__slider-item {
    color: #fff;
    width: 698px;
    padding-bottom: 20px;

  }

  &__slider-title {
    font-size: 36px;
    font-weight: 400;
    line-height: 44px;
    margin-bottom: 85px;
  }

  &__slider-text {
    font-size: 24px;
    font-weight: 300;
    line-height: 29px;
    width: 698px;
    margin: 0 auto;
  }

  &__slider-info {
    position: relative;
  }

  &__slider-img--right {
    position: absolute;
    bottom: -10%;
    right: 15%;

  }

  &__slider-img--left {
    position: absolute;
    top: -10%;
    left: 15%;

  }
}

.footer__icons {
  display: none;
}

@media (max-width: 1280px) {
  .nav {
    display: none;
  }

  .nav {
    &__active {
      display: block;
      margin-top: 45px;
    }

    &__active-inner {}

    &__active-list {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
    }

    &__active-item {}

    &__active-select {
      font-size: 22px;
      font-weight: 600;
      line-height: 27px;
      color: #FFEA00;
    }
  }



  .rightside-menu {
    &__title {
      text-align: right;
      margin-left: auto;
      font-size: 15px;
      font-weight: 400;
      line-height: 15px;
      color: #FFEA00;
      width: 80px;
      margin-top: 100px;
      margin-bottom: 20px;
    }

    &__social {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      gap: 7px;


    }

    &__social-link {}

    &__social-img {
      width: 20px;
      height: 20px;
    }

    &__telegramm-chat {
      display: block;
      width: auto;
      margin-left: auto;
      font-size: 10px;
      font-weight: 400;
      line-height: 12px;
      color: #fff;
    }
  }



  .nav {
    &__language {
      width: 108px;
      margin-left: auto;
    }

    &__language-link {
      color: #FFEA00;
      text-align: left;
    }

    &__language-link--active {
      font-family: 600;
    }



    &__list {
      flex-direction: column;
      margin: 0 auto;
      align-items: flex-end;
      gap: 12px;
      border: none;
    }
  }

  .rightside-menu {
   

    &__content {
      width: 284px;
      margin: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-end ;
      padding-right: 38px;
    }

    &__nav-phone {
      color: #FFEA00;
      margin-right: 0;
      margin-left: auto;
      width: 225px;
      display: block;
      margin-bottom: 100px  ;
    }

    &__nav-list {
      color: #fff;
    }

    &__nav-item {
      margin-bottom: 12px;
      text-align: right;
    }

    &__nav-link {
      color: inherit;
    }

  }

  .rightside-menu--close {
    display: block;
    margin-left: auto;
  }

  .gallery {
    &__inner {
      width: 100%;

      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(8, 1fr);
    }

    &__item {
      background-position: center;
    }

    &__item:nth-child(3),
    &__item:nth-child(9) {
      grid-row: span 2;
      grid-column: span 2;
    }
  }

  .reviews {
    &__slider-img--right {
      position: absolute;
      bottom: -10%;
      right: 0;

    }

    &__slider-img--left {
      position: absolute;
      top: -10%;
      left: 0;

    }
  }
}

@media(max-width: 980px) {

  .record {
    &__inner {
      flex-direction: column;
      align-items: center;


    }

    &__title-inner {
      height: 40%;
      width: 100%;
    }

    &__title {
      text-align: left;
      font-size: 40px;
      line-height: 40px;
      width: 305px;
      display: block;
      margin: 80px auto 80px 44px;

    }

    &__content {
      width: 100%;
      height: 60%;
      align-items: left;
    }

    &__content-title {
      text-align: left;
      font-size: 24px;
      line-height: 24px;
      margin-top: 60px;
    }


    &__content-form {
      margin-bottom: 40px;
      width: 90%;
    }

  }


  .info__text {
    max-width: 860px;
    width: 100%;
  }

  .footer {
    &__inner {}

    &__nav {
      display: none;
    }

    &__item {
      border: none;
      margin-left: 0;
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
      width: 100%;
    }

    &__contact {
      border-bottom: none;
      align-items: flex-end;
      margin: 43px 32px;
    }

    &__contact-link {}

    &__info {
      margin: 43px 32px;
    }

    &__info-img {}

    &__info-text {}

    &__social {
      display: none;
    }

    &__icons {
      margin-top: 18px;
      margin-bottom: 62px;
      display: flex;
      gap: 15px;
    }

    &__icons-link {}

    &__icons-img {}
  }

}

@media (max-width: 720px) {
  .header__title {
    max-width: 300px;
    width: 100%;
    font-size: 64px;
    font-weight: 400;
    line-height: 64px;
    color: #ffea00;
    margin: 183 auto 263px 30px;
  }

  .info {
    padding-right: 60px;

    &__title {
      font-size: 24px;
      line-height: 24px;
      margin-bottom: 20px;
    }

    &__text {
      font-size: 12px;
      line-height: 16px;

    }
  }

  .gallery__inner {
    height: 1482px;
  }

  .gallery {
    &__item-number {
      display: none;
    }

    &__item-title {
      bottom: 10px;
      left: 10px;
      font-size: 20px;
      line-height: 20px;
    }

    &__item-title--big {

      font-size: 40px;
      line-height: 40px;
    }

    &__item-link {
      display: none;
    }
  }


  .reviews {

    &__title {
      font-size: 32px;
      line-height: 32px;

      text-align: center;
      width: 100%;
      display: block;
      margin: 0 auto;
      margin-bottom: 70px;
    }

    &__slider-title {
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 19px;
    }

    &__slider-text {
      font-size: 11px;
      line-height: 11px;
      width: 300px;
    }

    &__slider-img--right {
      display: none;
    }

    &__slider-img--left {
      display: none;
    }


  }
}