html, body {
  height: 100%;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  color: $default;
  overflow-x: hidden;
  min-width: 320px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
  position: relative;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1794px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid{
  max-width: 1920px;
  padding: 0 10px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

